<template>
    <div class="phone-body">

        <div class="feed" v-if="step === 1" v-dragscroll>
            <vuegramPost
                v-for="(post,index) in posts"
                :key="index"
                :post="post"
            >{{ post }}</vuegramPost>
        </div>

        <div v-else-if="step === 2">
            <div 
                :style="{backgroundImage: 'url('+ image +')'}"
                :class="selectFilter"
                class="selected-image">
            </div>
            <div class="filter-container" v-dragscroll>    
                <FilterType 
                    v-for="filter in filters"
                    :key="filter.name"
                    :filter="filter"
                    :image="image"
   
                ></FilterType>       
            </div>
        </div>

        <div v-else-if="step === 3">
            <div 
                :style="{backgroundImage: 'url('+ image +')'}"
                :class="selectFilter"
                class="selected-image">
            </div>
            <div class="caption-container">
                <textarea 
                    placeholder="Write a caption"
                    :value="value"
                    @input="inputText"
                ></textarea>
            </div>
        </div>
    </div>
</template>

<script>
import vuegramPost from "./vuegramPost.vue"
import FilterType from "./FilterType.vue"
export default {
    props:{
        posts: Array,
        step: Number,
        image: String,
        filters: Array,
        selectFilter: String,
        value: String
    },
    components: {
        vuegramPost: vuegramPost,
        FilterType: FilterType
    },
    methods: {
        inputText(event){
            this.$emit("input", event.target.value)
        }
    }
}
</script>

<style lang="scss" src="../styles/phone-body.scss"></style>